<!--https://github.com/eligrey/FileSaver.js-->
<template>
    <div id="objectManager" class="px-3 py-2">
        <div v-if="selected[0]">
            <space-list v-if="selected[0].space" 
            :space="selected[0].space" 
            :id="selected[0].space.id"
            :embed="false" 
            :key="selected[0].space.id">
            </space-list>
            <connection-list v-if="selected[0].connection" 
            :connection="selected[0].connection" 
            :id="selected[0].connection.id" 
            :embed="false"
            :key="selected[0].connection.id">
            </connection-list>
        </div>

        <div v-else-if="analysisToggle">
        
        </div>

        <div v-else-if="topBoard">
            <board-list :board="topBoard" :id="topBoard.id" :key="topBoard.id">
            </board-list>
        </div>
    </div>
</template>

<script>
import { fileEventBus, objectEventBus } from '../main.js';
import { saveAs } from 'file-saver';
import DataService from '../dataservice';
import Board from '../classes/Board.js';
import BoardVue from './BoardVue.vue';
import Space from '../classes/Space.js';
import SpaceVue from './SpaceVue.vue';
import Connection from '../classes/Connection.js';
import ConnectionVue from './ConnectionVue.vue';
import Analysis from './Analysis.vue'
import uniqueID from 'lodash.uniqueid';

export default {
    data() {
        return {
            Board : [],
            Spaces : [],
            Connections : []
        }
    },
    props: {
        selected: {
            type: Array
        },
    },
    computed: {
        analysisToggle() {

        },
        topBoard() {
            return this.Board[this.Board.length - 1];
        },
        topSpace() {
            return this.Spaces[this.Spaces.length - 1];
        },
        topConnection() {
            return this.Connections[this.Connections.length - 1];
        }
    },
    components: {
        boardList : BoardVue,
        spaceList : SpaceVue,
        connectionList : ConnectionVue,
        analysis: Analysis
    },
    methods: {
        jsonUpload: function(json) {
            let board = json.Board[0];
            let b = new Board(board.name, this.topBoard.image);
            for (const prop in board) {
                b[prop] = board[prop];
            }
            b.image = this.topBoard.image;
            this.Board = [];
            this.Board.push(b);
            objectEventBus.selectBoard(this.Board[0]);
            
            this.Spaces = [];
            json.Spaces.forEach((space) => {
                let s = new Space(space)
                for (const prop in space) {
                    s[prop] = space[prop];
                }
                this.Spaces.push(s);
            });
            
            this.Connections = [];
            json.Connections.forEach((connection) => {
                let c = new Connection();
                for (const prop in connection) {
                    c[prop] = connection[prop];
                }
                this.Connections.push(c);
            });
            
            objectEventBus.jsonToCanvas({Board: this.Board, Spaces: this.Spaces, Connections: this.Connections, CanvasObjects: json.CanvasObjects});
        }
    },
    mounted() {
        this.buttonElement = Array.from(document.getElementsByName("sidebarButton")[0]);
    },
    created() {
        objectEventBus.$on('createBoard', (board) => {
            let lastTop = this.topBoard;
            this.Board.push(new Board(board.name, board.img));
            let id = (lastTop) ? lastTop.id + 1 : 1;
            this.topBoard.id = id;
            objectEventBus.selectBoard(this.topBoard);
            console.log("board created: " + this.topBoard.fileName, this.topBoard);
        })
        objectEventBus.$on('createSpace', (space) => {            
            let lastTop = this.topSpace;
            this.Spaces.push(space);
            let id = (lastTop) ? lastTop.id + 1 : 0; //todo: if you fucked everything up, change the 0 back to 1 
            this.topSpace.SetID(id);
            console.log("space created: ", this.topSpace);
        })
        objectEventBus.$on('delete', (obj) => {
            var index = -1;
            function spaceIndex (check) { return check.id == obj.space.id; }

            function connIndex (check) { return check.id == obj.connection.id; }

            //if this is a shape
            if (obj.space) {
                index = this.Spaces.findIndex(spaceIndex);
                if (index > -1) this.Spaces.splice(index, 1);
                //remove adjacent connections
                var toRemove = [];
                if (this.Connections) {
                    this.Connections.forEach(connection => {
                        if (connection.origin == obj) {
                            connection.origin = null;
                            toRemove.push(connection);
                        }
                        if (connection.target == obj) {
                            connection.target = null;
                            toRemove.push(connection);
                        }
                    });
                    for (var i = 0; i < toRemove.length; i++) {
                        this.Connections.splice(this.Connections.indexOf(toRemove[i]), 1);
                        objectEventBus.clearLines();
                    }
                }
            }

            //if this is a line
            if (obj.connection) {
                index = this.Connections.findIndex(connIndex);
                if (index > -1) this.Connections.splice(index, 1);
            }
        })
        objectEventBus.$on('createConnection', (connection) => {
            var copy = false;
            this.Connections.forEach(loopConnection => {
                if (connection.origin == loopConnection.origin && connection.target == loopConnection.target) copy = true;
            });
            if (!copy) {
                connection.SetID(uniqueID('connection-'));
                console.log("connection created: ", connection);
                this.Connections.push(connection);
                objectEventBus.connectionsList(this.Connections);
            }
        })
        objectEventBus.$on('getObjects', (obj) => {
            obj.Board = this.Board;
            obj.Spaces = this.Spaces;
            obj.Connections = this.Connections;
        })
        objectEventBus.$on('download', () => {
            let content = { Board: this.Board, Spaces: this.Spaces, Connections: this.Connections, CanvasObjects: []}
            content.Spaces.forEach(space => {
                //strip interrelationships
                content.CanvasObjects.push(space.shape);
                space.shape = space.shape.id;
            });
            content.Connections.forEach(connection => {
                //strip interrelationships
                connection.origin = connection.origin.id;
                connection.target = connection.target.id;
                connection.line.id = connection.id;
                content.CanvasObjects.push(connection.line);
                connection.line = connection.id;
                connection.triangle = null;
            });
            let filename = this.Board[0].fileName.split('.').slice(0, -1).join('.') + '.json';
            let file = new Blob([JSON.stringify(content, null, 2)], { type: 'application/json' });
            saveAs(file, filename);
            console.log('downloading ' + filename + '...');
        })
        objectEventBus.$on('upload', (file) => {
            let reader = new FileReader();
            let temp = this;
            reader.onload = function(event) {
                let json = JSON.parse(event.target.result);
                temp.jsonUpload(json);
                console.log('uploaded ' + file.name);
            }
            reader.readAsText(file);
        })
        objectEventBus.$on('submit', () => 
        {
            let img = fileEventBus.getImage()
            let content = { Board: this.Board, Spaces: this.Spaces, Connections: this.Connections, CanvasObjects: []}
            content.Spaces.forEach(space => {
                //strip interrelationships
                if (space.shape) space.shape.canvas = null
                space.shape = space.shape.id;
            })
            content.Connections.forEach(connection => {
                //strip interrelationships
                connection.origin = connection.origin.id;
                connection.target = connection.target.id;
                if (connection.line)
                {
                    connection.line.canvas = null
                    connection.line.id = connection.id;
                    connection.line = connection.id;
                    connection.triangle = null;
                }
            })
            let filename = content.Board.englishTitle || this.Board[0].fileName.split('.').slice(0, -1).join('.')
            let submissionData =
            {
                title: filename,
                board: content.Board,
                spaces: content.Spaces,
                connections: content.Connections,
                //canvas: content.CanvasObjects
            }
            DataService.create(img, submissionData)
        })
    }
}
</script>

