<template>
    <b-sidebar name="asidebar" id="asidebar" :width="width" title="Analysis" @shown="analyze" @hidden="reset" right shadow>
        <div style="padding: 10px">
            <b-card id="preconditions" no-body border="light" @click="reset">
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <b-button block v-b-toggle.preconditions-collapse :variant="preconditionsButton">Preconditions</b-button>
                </b-card-header>
                <b-collapse visible id="preconditions-collapse">
                    <b-card no-body :border-variant="cardColor" :header-border-variant="cardColor">
                        <b-list-group flush>
                            <b-list-group-item class="d-flex justify-content-between align-items-center">
                                <b-icon-check-circle v-if="containsFuridashi" variant="success"/>
                                <b-icon-x-circle v-else variant="danger"/>
                                 / Start
                            </b-list-group-item>
                            <b-list-group-item class="d-flex justify-content-between align-items-center">
                                <b-icon-check-circle v-if="containsAgari" variant="success"/>
                                <b-icon-x-circle v-else variant="danger"/>
                                 / End
                            </b-list-group-item>
                            <b-list-group-item class="d-flex justify-content-between align-items-center">
                                <b-icon-check-circle v-if="pathToEndExists" variant="success"/>
                                <b-icon-x-circle v-else variant="danger"/>
                                   path
                            </b-list-group-item>
                            <b-list-group-item class="d-flex justify-content-between align-items-center">
                                <b-icon-check-circle v-if="noConflictingChances" variant="success"/>
                                <b-icon-x-circle v-else variant="danger"/>
                                No jumping conflicts
                            </b-list-group-item>
                            <b-list-group-item class="d-flex justify-content-between align-items-center">
                                <b-icon-check-circle v-if="noConflictingSequences" variant="success"/>
                                <b-icon-x-circle v-else variant="danger"/>
                                No sequential conflicts
                            </b-list-group-item>
                        </b-list-group>
                    </b-card>
                </b-collapse>
            </b-card>
            <p />
            <b-form>
                
                <!-- <b-form-group
                    label="Simple Analysis"
                    label-class="font-weight-bold pt-0"
                    class="mb-0">
                </b-form-group>

                <b-form-group
                    label="Paths Analysis"
                    label-class="font-weight-bold pt-0"
                    class="mb-0">
                </b-form-group> -->

                <b-form-group
                    label="Agent-Based Analysis"
                    label-class="font-weight-bold pt-0"
                    class="mb-0">

                    <b-card no-body>
                        <b-card-header @click="agentControlsVisible = !agentControlsVisible" header="Controls"></b-card-header>
                        <b-collapse v-model="agentControlsVisible">
                            <b-card-body>
                            <b-form-group
                                label="# of Players"
                                label-size="sm"
                                :disabled="!readyToAnimate">
                                <b-form-input v-model="playerCount" size="sm"></b-form-input>
                            </b-form-group>
                            <b-form-group
                                label="Game ends on Agari"
                                label-size="sm">
                                <b-form-checkbox v-model="finishAnimationOnAgari" size="sm" :disabled="!readyToAnimate"></b-form-checkbox>
                            </b-form-group>
                            <b-form-group
                                label="Speed"
                                label-size="sm">
                                <!-- todo: currently useless -->
                                <b-form-input v-model="animationSpeed" size="sm" type="range" min="0" max="1" :disabled="true"></b-form-input>
                            </b-form-group>
                            <b-button v-if="doneAnimating" block variant="success" @click="simulate" :disabled="!readyToAnimate">
                                <b-icon-play-fill />
                            </b-button>
                            <b-button v-else block variant="danger" @click="reset">
                                <b-icon-x-octagon-fill />
                            </b-button>
                            </b-card-body>
                        </b-collapse>
                     </b-card>
                    <p />

                    <b-form-group
                        label="# of Players who reached Agari"
                        label-size="sm">
                    <b-form-input v-model="onAgari.length" size="sm" :disabled="true"/>
                    </b-form-group>
                    <b-form-group
                        label="Rounds until complete"
                        label-size="sm">
                    <b-form-input v-model="roundsUntilComplete" size="sm" :disabled="true"/>
                    </b-form-group>
                </b-form-group>
            </b-form>
        </div>
        <div style="padding: 10px">
        <b-button block @click="download" :disabled="!readyToAnimate || heatSpaces.length == 0">
            <b-icon-file-earmark-arrow-down /> <em> Download analysis json</em>
        </b-button>
        </div>
    </b-sidebar>
</template>

<script>
//todo:
// json download button
// there seems to be a pathfinding bug with henshin.json that's obvious
// paths permutations
// path thing vue
// find min heat max heat to more appropriately set heatToAdd
// make speed work and not break shit
// split this apart

import { fabric } from 'fabric'
import { saveAs } from 'file-saver'
import { objectEventBus } from '../main.js'
import Path from '../classes/Path.js'
import Player from '../classes/Player.js'

export default {
    mounted() {
        objectEventBus.$on('connectionsList', () => 
        {
            this.getObjects()
        })
        objectEventBus.$on('createSpace', () => 
        {
            this.getObjects()
        })
        objectEventBus.$on('delete', () =>
        {
            this.getObjects()
        })
    },
    created() {

    },
    data() {
        return {
            canvas: {},
            model: {},
            Board: [],
            Spaces: [],
            Connections: [],
            players: [],
            playerShapes: [],
            turnAt: 0,
            roundAt: 0,
            playerCount: 6,
            finishAnimationOnAgari: true,
            animationSpeed: 1,
            onAgari: [],
            firstOnAgari: [],
            doneAnimating: true,
            heatSpaces: [],
            preconditionsVisible: true,
            agentControlsVisible: true,
        }
    },
    computed: {
        width()
        {
            return '320px' //todo
        },
        furidashi() 
        {
            return this.Spaces.find(space => space.furidashi);
        },
        agari() 
        {
            return this.Spaces.find(space => space.agari);
        },
        containsFuridashi()
        {
            if (this.furidashi) return true
            return false
        },
        containsAgari()
        {
            if (this.agari) return true
            return false
        },
        isJumpingSugoroku() 
        {
            if (this.Connections.find(connection => connection.type == 'jumping')) return true
            else return false
        },
        paths()
        {
            return (this.findPaths(this.furidashi))
        },
        pathToEndExists() 
        {
            if (!this.furidashi || !this.agari) return false
            else if (this.paths.length == 0) return false
            else return true
        },
        numberOfSpaces() 
        {
            return this.Spaces.length
        },
        noConflictingChances()
        {
            //todo
            return true
        },
        noConflictingSequences()
        {
            //todo
            return true
        },
        isValid()
        {
            if (this.furidashi && this.agari && this.pathToEndExists && this.noConflictingChances && this.noConflictingSequences) {
                return true
            }
            return false
        },
        cardColor()
        {
            if (this.isValid) return "success"
            return "danger"
        },
        preconditionsButton()
        {
            if (this.isValid) return "outline-success"
            return "outline-danger"
        },
        heatToAdd()
        {
            return (1 / (this.playerCount)) * 2
            //return 0.1
        },
        readyToAnimate()
        {
            return (this.isValid && this.doneAnimating)
        },
        roundsUntilComplete()
        {
            let lastOnAgari = this.onAgari[this.onAgari.length - 1]
            if (lastOnAgari) return lastOnAgari.path.length
            return 0
        }
    },
    components: {

    },
    methods: {
        getObjects() 
        {
            let obj = {};
            objectEventBus.getObjects(obj);
            this.Board = obj.Board;
            this.Spaces = obj.Spaces;
            this.Connections = obj.Connections;
        },
        getCanvas()
        {
            let obj = {}
            objectEventBus.getCanvas(obj)
            this.canvas = obj.canvas 
        },
        analyze() 
        {
            this.reset()
            this.getObjects()
            this.getCanvas()

            console.log("analysis\ncontains furidashi: " + this.containsFuridashi + 
            "\ncontains agari: " + this.containsAgari + 
            "\nspaces: " + this.numberOfSpaces + 
            "\npath to end exists: " + this.pathToEndExists)
            
            if (!this.paths) return

            //todo: put this in its own function
            let pathsString = "non-looping paths (" + this.paths.length + ")\n"
            for (let i = 0; i < this.paths.length; i++)
            {
                pathsString += (i + 1) + ":\t"
                pathsString += this.paths[i].pathString() + "\n\t"
                pathsString += this.paths[i].connectionsString() + "\n"
            }
            console.log(pathsString, this.paths)
        },
        simulate()
        {
            this.reset()
            this.doneAnimating = false

            if (this.isValid) 
            {
                this.createHeat()
                this.createPlayers()
                this.animateNext()
            }
        },
        createHeat()
        {
            this.canvas.getObjects().forEach(shape =>
            {
                let heatSpace = false
                if (shape.type == 'ellSpace') 
                {
                    heatSpace = new fabric.EllSpace({
                        rx: shape.rx,
                        ry: shape.ry
                    })
                }
                else if (shape.type == 'rectSpace') 
                {
                    heatSpace = new fabric.RectSpace({
                        width: shape.width,
                        height: shape.height
                    })
                }

                if (heatSpace)
                {
                    heatSpace.heat = 0

                    heatSpace.id = shape.id
                    heatSpace.left = shape.left
                    heatSpace.top = shape.top

                    heatSpace.selectable = false
                    heatSpace.strokeWidth = 0
                    heatSpace.opacity = 0
                    heatSpace.fill = 'rgba(0,0,0,0.66)'

                    shape.heatSpace = heatSpace
                    this.heatSpaces.push(heatSpace)
                    this.canvas.add(heatSpace)
                }
            })
        },
        createPlayers()
        {
            for (let i = 0; i < this.playerCount; i++)
            {
                let player = new Player(this.furidashi, this.Connections)
                this.players.push(player)
                let playerShape = new fabric.PlayerShape({ id: i + 1 }, player, this.furidashi);
                this.playerShapes.push(playerShape)
                this.canvas.add(playerShape)
            }
            console.log("players (" + this.playerCount + ")", this.players)
        },
        animateNext()
        {
            this.turnAt += 1
            if (this.turnAt > this.playerCount - 1) 
            {
                if (this.onAgari.length > 0)
                {
                    if (this.firstOnAgari.length == 0) 
                        this.onAgari.forEach(player => this.firstOnAgari.push(player))

                    if (this.finishAnimationOnAgari || this.onAgari.length == this.playerCount)
                    {
                        this.finishAnimation()
                        return
                    }
                }
                this.turnAt = 0
                this.roundAt += 1
            }

            let playerShape = this.playerShapes[this.turnAt]
            if (!playerShape) return
            if (playerShape.animationAt > playerShape.path.length - 1)
            {
                this.animateNext()
            } else {
                let spaceOn = playerShape.path[playerShape.animationAt]
                if (spaceOn.agari) 
                    this.onAgari.push(playerShape)

                spaceOn.shape.heatSpace.opacity += this.heatToAdd
                spaceOn.shape.heatSpace.heat += 1

                this.animateShape(playerShape, this.animateNext)
            }
        },
        animateShape(shape, callback)
        {
            if (shape.animationAt > shape.path.length - 1)
            {
                callback()
                return
            }

            if (shape.animationAt == 0)
            {
                shape.animationAt += 1
                callback()
                return
            }

            let animationSpeed = this.animationSpeed
            let newPosition = shape.findNewPosition()

            shape.animate({'left': newPosition.left, 'top': newPosition.top },
            {
                duration: animationSpeed,
                onChange: this.canvas.renderAll.bind(this.canvas),
                onComplete: function() 
                {
                    shape.animationAt += 1
                    callback()
                },
                easing: fabric.util.ease.easeOutCirc
            })
        },
        finishAnimation()
        {
            this.doneAnimating = true
            console.log("players who reached agari in (" + this.firstOnAgari[0].animationAt + ") turn(s) (" + this.firstOnAgari.length + ")", this.firstOnAgari)
            console.log((this.roundAt + 1) + " rounds to completion")
        },
        findPaths(node, unvisitedEdges = [], visitedNodes = [], connectedEdges = [], paths = []) 
        {
            if (!node) return

            if (unvisitedEdges.length == 0 && visitedNodes.length == 0) 
                unvisitedEdges = [...this.Connections]

            if (!visitedNodes.includes(node)) 
            {
                visitedNodes.push(node)
            }

            if (node.agari == true) 
            {
                paths.push(new Path(visitedNodes, connectedEdges))
                return paths
            }

            let adjacentEdges = []
            unvisitedEdges.forEach(edge => { if (edge.origin.id == node.id) adjacentEdges.push(edge) })

            adjacentEdges.forEach(edge => 
            {
                unvisitedEdges.splice(unvisitedEdges.indexOf(edge), 1) //remove edge from unvisitedEdges
                let nextNode = edge.target.space
                if (visitedNodes.includes(nextNode)) {
                    this.findPaths(node, [...unvisitedEdges], [...visitedNodes], [...connectedEdges], paths);
                }
                else 
                {
                    connectedEdges.push(edge)
                    this.findPaths(nextNode, [...unvisitedEdges], [...visitedNodes], [...connectedEdges], paths);
                }
                connectedEdges.pop()
            })
            return paths;
        },
        reset()
        {
            if (this.players) this.players = []
            this.playerShapes.forEach(shape => { this.canvas.remove(shape) })
            this.heatSpaces.forEach(space => { this.canvas.remove(space) })
            this.turnAt = 0
            this.roundAt = 0
            this.playerShapes = []
            this.heatSpaces = []
            this.firstOnAgari = []
            this.onAgari = []
            this.doneAnimating = true
        },
        download()
        {
            //todo: trash
            let content = { numberOfSpaces: this.numberOfSpaces, paths: [...this.paths], players: [...this.players],  finishedInTurns: this.firstOnAgari[0].animationAt }
            let filename = this.Board[0].fileName.split('.').slice(0, -1).join('.') + "analysis.json"
            console.log(content)
            content.paths.forEach(path => 
            {
                path.connections.forEach(connection => 
                {
                    connection.origin = connection.origin.id
                    connection.target = connection.target.id
                    connection.line.id = connection.id
                    connection.line = connection.id
                    connection.triangle = null
                })
                path.spaces.forEach(space => 
                {
                    space.shape = space.shape.id
                })
            })

            let file = new Blob([JSON.stringify(content, null, 2)], { type: 'application/json' })
            saveAs(file, filename)
            console.log('downloading ' + filename + '...');
        }
    },
}
</script>

