import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      alias: "/sugoroku",
      name: "sugoroku",
      component: () => import("./components/SugorokuList")
    },
    {
      path: "/add",
      name: "add",
      component: () => import("./components/SugorokuAdd")
    }
  ]
});