// why are other ids '1' while connection ids are 'connection-1'

export default class Connection {
    origin = {};
    target = {};
    line = {};
    type = 'sequential'; //'sequential' or 'jumping'
    
    id = ''; //added by ObjectManager.vue
    chances = []
    //chance = 1;
    //chanceType = "on"; //'on', 'over', 'under', 'unless'

    constructor(origin, target, line, type = 'sequential') 
    {
        this.origin = origin
        this.target = target
        this.line = line
        this.type = type
        this.triangle = ''

        this.chances.push({ value: 1, chanceType: 'on', index: 0 })

        if (this.line) this.setLineColor()
    }

    //setLineColor()
    //called from constructor, as well as from outside
    setLineColor() {
        this.line.set({'stroke': (this.type == 'sequential') ? 'red' : 'yellow'})
    }

    SetID(id) {
        this.id = id;
        this.line.id = id;
        console.log(this.line);
    }
    
    get type() {
        return this.type;
    }

    get origin() {
        return this.origin;
    }

    get target() {
        return this.target;
    }

    get length() {
        let origin = { 
            x: this.origin.aCoords.tl.x + (this.origin.aCoords.br.x / 2),
            y: this.origin.aCoords.br.y + (this.origin.aCoords.tl.y / 2)
        };
        let target = { 
            x: this.target.aCoords.tl.x + (this.target.aCoords.br.x / 2),
            y: this.target.aCoords.br.y + (this.target.aCoords.tl.y / 2)
        };
        return Math.sqrt(Math.pow(origin.x - target.x, 2) + Math.pow(origin.y - target.y,2))
    }
}