//https://firebase.google.com/docs/storage/web/upload-files#web-version-8
import { fabric } from 'fabric'
import firebase from "./firebase"
const db = firebase.db.ref()
const store = firebase.store.ref()

class DataService {
  getAll() {
    return db;
  }

  async createThubmnail(img)
  {
    return new Promise(
      (resolve) =>
      {
        let thumbnail = {}
        let imgReader = new FileReader()
        imgReader.onload = function(read)
        {
          let url = read.target.result
          fabric.Image.fromURL(url, 
            (img) =>
            {
              const max = 128
              let scale = Math.min(max / img.width, max / img.height)
              img.filters.push(new fabric.Image.filters.Resize(
              {
                scaleX: scale,
                scaleY: scale
              }))
              img.applyFilters()
              thumbnail = img._element.toDataURL()
              resolve(thumbnail)
          })
        }
        imgReader.readAsDataURL(img.file)
      })
  }

  async imageUpload(img, name)
  {
    console.log("trying to upload ", name)
    return new Promise(
    (resolve, reject) =>
    {
      let imageUpload = {}
      if (typeof(img) == 'string') imageUpload = store.child(name).putString(img, 'data_url')
      else imageUpload = store.child(name).put(img.file)
      imageUpload.on('state_changed',
      (snapshot) =>
      {
        const progress = Math.ceil((snapshot.bytesTransferred / snapshot.totalBytes) * 100)
        console.log(progress + '% uploaded')
      },
      (error) => {
        console.log(error)
        reject(error)
      },
      async () =>
      {
        const url = await imageUpload.snapshot.ref.getDownloadURL()
        console.log('uploaded to ' + url)
        resolve(url)
      })
    })
  }

  async create(img, content) {
    //create thumbnail image
    let thumbnail = await new Promise(
    (resolve) => 
    {
      this.createThubmnail(img).then(
        (thumb) =>
        {
          resolve(thumb)
        }
      )
    })
    
    //upload img
    content.url = await new Promise((resolve) =>
    {
      this.imageUpload(img, content.title).then(
        (url) =>
        {
          resolve(url)
        }
      ).catch(
      (error) =>
      {
  
      }
    )
    })

  //upload thumb to store, save url
  content.url_thumb = await new Promise((resolve) =>
  {
    this.imageUpload(thumbnail, content.title + '_thumb').then(
      (url) =>
      {
        resolve(url)
      }
    ).catch(
      (error) =>
      {
  
      }
    )
  })
  
  //upload content to database
  db.push(content).then(
  () =>
  {
      console.log("submitted", content.title, " successfully: ")
      console.log(content)
  }).catch(
  (e) =>
  {
      console.log(e)
  })
  }

  update(key, value) {
    return db.child(key).update(value);
  }

  delete(key) {
    return db.child(key).remove();
  }

  deleteAll() {
    return db.remove();
  }
}

export default new DataService();
