//DrawConnection
//helper class, takes ImageCanvas
//uses ImageCanvas.connections (list of Connections) and ImageCanvas.canvas (fabric Canvas)
//responsible for drawing lines on Canvas representative of those Connections
export default class DrawConnection {
    imageCanvas = [];

    constructor(imageCanvas) {
        this.imageCanvas = imageCanvas;

        this._lineMovement();
        this._newLines();
        this._clearLines();
    }
    
    jsonConnection(connection) {
        console.log(connection);
    }
    
    //lineMovement()
    //called by constructor
    //draws lines on Canvas representative of Connections whenever a fabric object on the Canvas moves
    _lineMovement() {
        let temp = this;
        this.imageCanvas.canvas.on('object:moving', function() {
            if (!temp.imageCanvas.connections) return;
            //for each connection,
            temp.imageCanvas.connections.forEach((connection) => {
                //draw a straight line if the connection has no opposite in connections
                let coords = temp._lineCoords(connection);
                connection.line.set({ 'x1': coords[0].x, 'y1': coords[0].y, 'x2': coords[1].x, 'y2': coords[1].y});
                connection.triangle.set(temp._trianglePosition(connection));
                temp.imageCanvas.canvas.sendToBack(connection.triangle);
                temp.imageCanvas.canvas.sendToBack(connection.line);
                temp.imageCanvas.canvas.requestRenderAll();
            });
            if (temp.imageCanvas.canvas.board) temp.imageCanvas.canvas.sendToBack(temp.imageCanvas.canvas.board);
        });
    }

    //newLines()
    //called by constructor
    _newLines() {
        objectEventBus.$on('connectionsList', () => {
            if (!this.imageCanvas.connections) return;
            this.imageCanvas.connections.forEach((connection) => {
                    if (connection.line) {
                        this.imageCanvas.canvas.remove(connection.line);
                        this.imageCanvas.canvas.remove(connection.triangle);
                    }

                    connection.line = this._makeLine(connection);
                    connection.triangle.set(this._trianglePosition(connection));
                    
                    this.imageCanvas.canvas.add(connection.line);
                    this.imageCanvas.canvas.add(connection.triangle);

                    this.imageCanvas.canvas.sendToBack(connection.triangle);
                    this.imageCanvas.canvas.sendToBack(connection.line);
                    this.imageCanvas.canvas.sendToBack(this.imageCanvas.canvas.board);
            });
        });
    }

    //makeLine()
    //called from various functions in DrawConnection
    _makeLine(connection) {
        let coords = this._lineCoords(connection);
        let line = new fabric.Connection([coords[0].x, coords[0].y, coords[1].x, coords[1].y], {
            stroke: (connection.type == 'sequential') ? 'red' : 'yellow',
            strokeWidth: 5,
            hasControls: false,
            selectable: true,
            evented: true,
            connection: connection
        });
        let triangle = new fabric.Triangle({
            centeredRotation: true,
            originX: "center",
            originY: "center",
            width: 20,
            height: 15,
            selectable: false,
        });
        connection.triangle = triangle;
        return line;
    }

    _trianglePosition(connection) {
        let line = connection.line;
        let coords = this._lineCoords(connection);
        let xDiff = line.x2 - line.x1;
        let yDiff = line.y2 - line.y1;
        let angle = (Math.atan2(yDiff, xDiff) * 180 / Math.PI) + 90;
        return {'stroke': line.stroke, 'fill': line.stroke, 'left': coords[1].x, 'top': coords[1].y, 'angle': angle}
    }

    //lineCoords()
    //called from various functions in DrawConnection
    _lineCoords(connection) {
        let pt1 = new fabric.Point(connection.origin.aCoords.tl.x + (connection.origin.width / 2), connection.origin.aCoords.tl.y + (connection.origin.height / 2));
        let pt2 = new fabric.Point(connection.target.aCoords.tl.x + (connection.target.width / 2), connection.target.aCoords.tl.y + (connection.target.height / 2));
        return [pt1, pt2];
    }

    //clearLines()
    //called by constructor
    //deletes lines that are not valid
    _clearLines() {
        objectEventBus.$on('clearLines', () => {
            if (this.imageCanvas.canvas.getObjects()) {
                var temp = this.imageCanvas;
                temp.canvas.getObjects().forEach(function (o) {
                    if (o.connection) {
                        if (o.connection.target == null || o.connection.origin == null)
                        temp.canvas.remove(o);
                        temp.canvas.remove(o.connection.triangle);
                    }
                });
            }
        });
    }
}