export default class Board {
    //neccesary
    fileName = "";
    image = []; //Image
    
    title = ""; //String
    id = 0; //added by ObjectManager.vue
    movementType = '';
    chanceMechanism = '';
    type = [];
    spaces = {}

    //unneccesary
    englishTitle = '';
    date = '';
    height = '';
    width = '';
    artist = '';
    publisher = '';
    theme = [];
    material = null;
    otherMaterial = '';

    constructor (fileName, image) {
        this.fileName = fileName;
        this.image = image;
    }

    set fileName(fileName) {
        this.fileName = fileName;
    }

    set title(title) {
        this.title = title;
    }
    
    set image(image) {
        this.image = image;
    }

    set id(id) {
        this.id = id;
    }

    set dice(dice) {
        this.dice = dice;
    }

    set furidashi(space) {
        this.furidashi = space;
    }

    set agari(space) {
        this.agari = space;
    }

    set spaces(spaces) {
        this.spaces = spaces;
    }

    get fileName() {
        return this.fileName;
    }

    get title() {
        return this.title;
    }

    get id() {
        return this.id;
    }

    get image() {
        return this.image;
    }
    
    get spacesAmount() {
        return this.spaces.length;
    }

    get spaces() {
        return this.spaces;
    }

    trimTitle(title) {
        return title.split('.').slice(0, -1).join('.');
    }

    spaceAt(index) {
        return this.spaces[index];
    }

    addSpace(space) {
        this.spaces.push(space);
    }

    removeSpace(index) {
        this.spaces = this.spaces.splice(index, 1);
    }

    clearSpaces() {
        this.spaces = [];
    }
}