<script>
//import Space from '../classes/Space.js';

export default {
    data() {
        return {

        }
    },
    methods: {
        furidashi: function(checked) {
            if (checked) {
                if (this.space.agari) this.space.agari = false;
                this.space.shape.ChangeLabel(' / Start');
            }
            else if (this.space.agari) this.space.shape.ChangeLabel(' / End')
            else this.space.shape.ChangeLabel('');
        },
        agari: function(checked) {
            if (checked) {
                if (this.space.furidashi) this.space.furidashi = false;
                this.space.shape.ChangeLabel(' / End');
            }
            else if (this.space.furidashi) this.space.shape.ChangeLabel(' / Start'); 
            else this.space.shape.ChangeLabel('');
        }
    },
    props: {
        space: Object,
        id: Number,
        embed: Boolean
    }
}
