export default class Space {
    index = 0;
    shape = {};

    id = ""; //added by ObjectManager.vue
    number = "";
    furidashi = false;
    agari = false;
    exact = true;
    description = "";
    instructionText = "";
    instructionTranslation = "";
    otherText = "";
    otherTranslation = "";

    constructor(shape) {
        this.shape = shape;
    }

    get id() {
        return this.id;
    }

    set id(id) {
        this.id = id;
        this.shape.id = id;
    }

    SetID(id) {
        this.id = id;
        this.shape.id = id;
    }
}