    <div>
        <b-form :id="id">
            <!-- origin, target, chance strings, distance -->
            <b-form-group
                label="Connection Info"
                label-class="font-weight-bold pt-0"
                class="mb-0">
                <b-form-group
                    id="input-group-type"
                    label="Type"
                    label-for="input-type"
                    label-size="sm">
                </b-form-group>
                <b-form-select
                    id="input-type"
                    size="sm"
                    v-model="connection.type" :options="connectionOptions.connectionType" @change="setLineColor">
                </b-form-select>

                <p />

                <div v-if="connection.type == 'jumping'">
                    <b-form-group
                        label="Chance(s)"
                        label-size="sm">
                        <div v-for="chance in connection.chances" :key="chance.index">
                            <b-card no-body>
                                <b-list-group flush>
                                    <b-list-group-item>
                                    <b-form-select
                                        id="input-chance-type"
                                        size="sm"
                                        v-model="chance.chanceType" :options="connectionOptions.chanceType">
                                    </b-form-select>
