                                    </b-list-group-item>
                                </b-list-group>
                            </b-card>
                            <p />
                        </div>
                        <b-button pill @click="addConnection" size="sm"> 
                            <b-icon-plus />
                        </b-button> {{ " " }}
                        <b-button pill @click="connection.chances.pop()" :disabled="connection.chances.length < 2" size="sm">
                            <b-icon-dash />
                        </b-button>
                    </b-form-group>
                </div>


                <b-form-group
                    id="input-group-length"
                    label="Length"
                    label-for="input-length"
                    label-size="sm">
                <b-form-input
                    id="input-length"
                    size="sm"
                    v-model="length"
                    disabled>
                </b-form-input>
                </b-form-group>
