<script>
//todo: 1 - 6 on chance number
// todos on Connection.js

export default {
    data() {
        return {
            connectionOptions: {
                connectionType: [
                    { value: "sequential", text: "Sequential"},
                    { value: "jumping", text: "Jumping"}
                ],
                chanceType: [
                    { value: "on", text: "On"},
                    { value: "over", text: "Over"},
                    { value: "under", text: "Under"},
                    { value: "unless", text: "Unless"}
                ]
            }
        }
    },
    props: {
        connection: Object,
        id: String,
        embed: Boolean
    },
    methods: {
        setLineColor: function() {
            this.connection.setLineColor();
        },
        addConnection: function() 
        {
            this.connection.chances.push({ value: 1, chanceType: 'on', index: this.connection.chances.length})
        }
    },
    computed: {
        title: function() {
            return this.originName + "  " + this.targetName;
        },
        originName: function() {
            return (this.connection.origin.space.number != "") ? this.connection.origin.space.number.toString() : this.connection.origin.space.id;
        },
        targetName: function() {
            return (this.connection.target.space.number != "") ? this.connection.target.space.number.toString() : this.connection.target.space.id;
        },
        length: function() {
            return this.connection ? this.connection.length.toFixed(2) + " px" : 0;
        }
    }
}
</script>

