import Path from './Path.js'


export default class Player {
    board
    spaceOn
    path = []
    rolls = []
    toMoveForward
    toMoveBackward
    done

    constructor(furidashi, connections, debug = false)
    { 
        this.board = connections
        this.spaceOn = furidashi
        this.path.push(furidashi)

        this.debug = debug

        while (!this.done) 
            this.roll()
    }

    roll() 
    {
        if (this.done)
            return

        let roll = Math.floor(Math.random() * 6) + 1 // 1 - 6
        this.rolls.push(roll)
        this._debug("rolled " + roll)
        this._planMove(roll)
    }

    _planMove(roll) 
    {
        this._debug("planMove " + roll + " on " + this.spaceOn.id)
        let next = this.spaceOn
        let jumpingConnections = this._findJumpingConnections(this.spaceOn)
        if (jumpingConnections.length > 0) 
        {
            jumpingConnections.forEach(connection => 
                {
                    connection.chances.forEach(chance => 
                        {
                        switch(chance.chanceType) 
                        {
                            case 'on':
                                if (roll == chance.value) next = connection.target.space;
                                break
                            case 'over':
                                if (roll > chance.value) next = connection.target.space;
                                break
                            case 'under':
                                if (roll < chance.value) next = connection.target.space;
                                break
                            case 'unless':
                                if (roll != chance.value) next = connection.target.space;
                                break
                            default:
                                break
                        }
                    })
                })
        } 
        if (next != undefined) next = (this._spaceAhead(roll) || next)
        this._move(next)
    }

    _move(space) 
    {
        this._debug("move to " + space.id)
        if (this.animated) 
        {
            //todo: move from this.spaceOn to space
        }
        this.spaceOn = space
        this.path.push(this.spaceOn)

        if (this.spaceOn.agari) this.done = true
    }

    _spaceAhead(amount) 
    {
        this._debug("spaceAhead " + amount + " from " + this.spaceOn.id)
        let space = this.spaceOn
        while (amount > 0)
        {
            if (space.agari)
            {
                if (space.exact) return this._spaceBehind(space, amount)
                return space
            }
            let connection = this._findSequentialConnection(space)
            if (!connection) return false
            space = connection.target.space
            amount--
        }
        return space
    }

    _spaceBehind(from, amount) 
    {
        this._debug("spaceBehind " + amount + " from " + from.id)
        let space = from
        while (amount > 0)
        {
            if (space.furidashi) return space
            let connection = this._findReverseConnection(space)
            space = connection.origin.space
            amount--
        }
        return space
    }

    _findSequentialConnection(space) 
    {
        this._debug("findSequentialConnection from " + space.id)
        let sequentialConnection = false
        this.board.forEach(connection => 
            {
                if (connection.origin.id == space.id && connection.type == 'sequential') 
                {
                    sequentialConnection = connection
                }
            })
        return sequentialConnection
    }

    _findJumpingConnections(space) 
    {
        this._debug("findJumpingConnections")
        let connections = []
        this.board.forEach(connection => 
            {
                if (connection.origin.id == space.id && connection.type == 'jumping') 
                {
                    connections.push(connection)
                }
            })
        return connections
    }

    _findReverseConnection(space)
    {
        this._debug("findReverseConnection from " + space.id)
        let sequentialConnection = {}
        this.board.forEach(connection =>
            {
                if (connection.target.id == space.id && connection.type == 'sequential') 
                {
                    sequentialConnection = connection
                }
            })
        return sequentialConnection
    }

    _debug(string)
    {
        if (this.debug) console.log(string)
    }
}