import { fabric } from 'fabric';

//Sequential Connection
fabric.Connection = fabric.util.createClass(fabric.Line, {
    type: 'connection',
    async: true,

    initialize: function(element, options) {
        options || (options = { });

        this.callSuper('initialize', element, options);
        this.set('id', options.id || '');
    },
    toObject: function() {
        return fabric.util.object.extend(this.callSuper('toObject'), {
            id: this.get('id'),
        });
    },
    fromObject: function(object, callback) {
        return fabric.Object._fromObject('connection', object, callback);
    },
    //TODO: Chance values
    _render: function(ctx) {
        this.callSuper('_render', ctx);
    }
});

//Rectangle Space
//todo: add more text rendering logic to _render OR remove text rendering logic from _render, add to DrawConnections or something similar. probably the latter
//todo: normalize on mouseup
fabric.RectSpace = fabric.util.createClass(fabric.Rect, {
    type: 'rectSpace',
    initialize: function(options) {
        options || (options = { });

        this.callSuper('initialize', options);
        this.set('label', options.label || '');
        this.set('id', options.id || '');
    },
    toObject: function() {
        return fabric.util.object.extend(this.callSuper('toObject'), {
            label: this.get('label'),
            id: this.get('id'),
        });
    },
    fromObject: function(object, callback) {
        return fabric.Object._fromObject('rectSpace', object, callback);
    },  
    _render: function(ctx) {
        this.callSuper('_render', ctx);
        //this.Normalize();
        ctx.font = '24px Helvetica';
        ctx.fillStyle = '#333';
        ctx.textAlign = "center";
        ctx.fillText(this.label, 0, 0);
    },
    ChangeLabel: function(label) {
        this.label = label;
        this.dirty = true;
        this.canvas.requestRenderAll();
    },
    SetID: function(id) {
        this.id = id;
    },
    GetID: function() {
        return this.id;
    },
    //todo: come back to this
    Normalize: function() {
        let scale = this.getObjectScaling();
        this.width *= scale.scaleX;
        this.height *= scale.scaleY;
        this.scale(1);
    }
});

