fabric.PlayerShape = fabric.util.createClass(fabric.Ellipse,
    {
        type: 'playerShape',
        initialize: function(options, player, furidashi)
        {
            options || (options = { })
            
            this.animationAt = 0

            this.callSuper('initialize', options)
            this.selectableselectable = options.selectable || false
            this.fill = options.fill || '#facade' //todo
            this.stroke = options.stroke || '#ff9999' //todo
            this.strokeWidth = options.strokeWidth || '2'

            this.id = options.id || ''
            
            this.path = player.path

            this.left = options.left || this.findNewPosition().left
            this.top = options.top || this.findNewPosition().top

            let size = (furidashi.shape.width || furidashi.shape.rx) * 0.2
            this.rx = options.rx || size
            this.ry = options.ry || size


        },
        _render: function(ctx) {
            this.callSuper('_render', ctx)
            ctx.font = '24px Helvetica'
            ctx.fillStyle = "#333"
            ctx.textAlign = "center"
            ctx.fillText(this.id, 0, 5)
        },
        findNewPosition: function()
        {
            let newSpace = this.path[this.animationAt].shape
            let width = (newSpace.width || newSpace.rx)
            let height = (newSpace.height || newSpace.ry)
            let xOffset = Math.floor((Math.random() * width) * 0.8) + (width * 0.1)
            let yOffset = Math.floor((Math.random() * height) * 0.8) + (height * 0.1) 
            
            let newPosition = { 
                'left': newSpace.left + xOffset,
                'top': newSpace.top + yOffset 
            }

            return newPosition
        }
    })

