import { fabric } from 'fabric';

export default class ImageContainer {
    constructor(canvas, img, callback) {
        this.canvas = canvas;
        this.img = img;

        let temp = this;
        fabric.Image.fromURL(this.img.img.src, function(i) {
            let img1 = i.set( {
            left: 0,
            top: 0,
            width: temp.img.img.width, 
            height: temp.img.img.height,
            scaleX: 1,
            scaleY: 1,
            hasControls: false,
            hasBorders: false,
            selectable: false,
            evented: false,
            hoverCursor: "default"
            });
            temp.canvas.board = img1;
            temp.canvas.add(img1);
            temp.canvas.sendToBack(img1);
            callback();
        });
    }
}