import { fabric } from 'fabric';
import { objectEventBus } from '../main.js';
import Space from './Space.js';
import Connection from './Connection.js';
import Shapes from './Shapes.js';

//DrawShape
//helper class, takes Phaser.Canvas
//binds functions for mouse:down, mouse:move, and mouse:up on the Canvas
//so that the user can draw a shape, depending on this.canvas.activeTool
export default class DrawShape {
    canvas = [];

    constructor(canvas) {
        this.canvas = canvas;
        Shapes; //adds custom shape subclasses to fabric object
        this.mouseDown();
        this.mouseMove();
        this.mouseUp();
    }

    //mouseDown()
    //called once by constructor
    mouseDown() {
        let canvas = this.canvas;
        canvas.on('mouse:down', function(o) {
            if (canvas.activeTool == null) return;
            if (o.target != null) return;

            let pointer = canvas.getPointer(o.e);
            this.isDown = true;
            this.selection = false;

            //the following are bound To the canvas, to be used by mouseMove() and mouseUp()
            //it's kind of nasty, but it works for now
            this.origX = pointer.x;
            this.origY = pointer.y;

            //depending on canvas.activeTool, bind this.shape to the canvas
            if (canvas.activeTool == 'drawRect') {
                this.shape = new fabric.RectSpace({
                    left: this.origX,
                    top: this.origY,
                    lockScalingFlip: true,
                    originX: 'left',
                    originY: 'top',
                    width: pointer.x-this.origX,
                    height: pointer.x-this.origY,
                    angle: 0,
                    stroke: 'red',
                    label: '',
                    fill: 'rgba(255,0,0,0.5)', //TODO: color from outside!
                    transparentCorners: false
                });
            }

            if (canvas.activeTool == 'drawEllipse') {
                this.shape = new fabric.EllSpace({
                    left: this.origX,
                    top: this.origY,
                    lockScalingFlip: true,
                    originX: 'left',
                    originY: 'top',
                    rx: pointer.x-this.origX,
                    ry: pointer.x-this.origY,
                    angle: 0,
                    fill: 'rgba(255,0,0,0.5)', //TODO: color from outside!
                    stroke: 'red',
                    transparentCorners: false
                });
            }

            if (canvas.activeTool == 'drawLine' || canvas.activeTool == 'drawJump') {
                let point = [this.origX, this.origY, pointer.x, pointer.y];
                this.shape = new fabric.Connection(point, {
                    stroke: (canvas.activeTool == 'drawLine') ? 'red' : 'yellow',
                    strokeWidth: 3,
                    selectable: false,
                    evented: false
                });
            }
            //add the shape to the canvas
            canvas.add(this.shape);
        });
    }

    //mouseMove()
    //called once by constructor
    mouseMove() {
        let canvas = this.canvas;
        canvas.on('mouse:move', function(o) {
            if (!this.isDown) return;

            let pointer = canvas.getPointer(o.e);
            
            if (canvas.activeTool == 'drawRect' || canvas.activeTool == 'drawEllipse') {
                if (this.origX > pointer.x) this.shape.set({ left: Math.abs(pointer.x) });
                if (this.origY > pointer.y) this.shape.set({ top: Math.abs(pointer.y) });

                let x = Math.abs(this.origX - pointer.x);
                let y = Math.abs(this.origY - pointer.y);
                if (this.shift) { //check if shift is being held down - bound from elsewhere onto canvas
                    let smallest = (x >= y) ? x : y;
                    if (canvas.activeTool == 'drawRect') this.shape.set({ width: smallest, height: smallest});
                    if (canvas.activeTool == 'drawEllipse') this.shape.set({ rx: smallest, ry: smallest});
                } else {
                    if (canvas.activeTool == 'drawRect') this.shape.set({ width: x, height: y});
                    if (canvas.activeTool == 'drawEllipse') this.shape.set({ rx: x, ry: y});
                }
            } 
            else if (canvas.activeTool == 'drawLine' || canvas.activeTool == 'drawJump') {
                this.shape.set({ x2: pointer.x, y2: pointer.y });
            }
            canvas.renderAll();
        });
    }

    //mouseUp()
    //called once by constructor
    mouseUp() {
        let canvas = this.canvas;
        canvas.on('mouse:up', function(o) {
            this.isDown = false;

            if (this.shape) {
                let objects = canvas.getObjects();
                let lastObject = objects[objects.length - 1];
                if (objects.length == 0) return;
                if (canvas.activeTool) {
                    //this.shape.set({ stroke: 'red', strokeWidth: 1,}); //TODO: color from outside!
                }
                
                if (canvas.activeTool == 'drawRect' || canvas.activeTool == 'drawEllipse') {
                    if (lastObject.width <= 20 || lastObject.height <= 20) { //TODO: make a % of board image size
                        canvas.remove(lastObject);
                        this.shape = null;
                    }

                    if (this.shape && !this.shape.space) {
                        this.shape.space = new Space(this.shape);
                        objectEventBus.createSpace(this.shape.space);
                    }
                }

                if (canvas.activeTool == 'drawLine' || canvas.activeTool == 'drawJump') {
                    if (this.shape && !this.shape.connection) {
                        let pointer = canvas.getPointer(o.e);
                        let pt1 = new fabric.Point(this.origX, this.origY);
                        let pt2 = new fabric.Point(pointer.x, pointer.y);
                        let origin = null;
                        let target = null;

                        objects.forEach(shape => {
                            if (shape.containsPoint(pt1, undefined, true) && shape.fill != "rgb(0,0,0)") origin = shape;
                            if (shape.containsPoint(pt2, undefined, true) && shape.fill != "rgb(0,0,0)") target = shape; 
                        });

                        if (origin && target && origin != target) {
                            //this.shape.connection = new Connection(origin, target, this.shape);
                            let connection = new Connection(origin, target, this.shape, (canvas.activeTool == 'drawLine') ? 'sequential' : 'jumping');
                            objectEventBus.createConnection(connection);
                        }
                        canvas.remove(lastObject);
                        this.shape = null;
                    } else {
                        canvas.remove(lastObject);
                        this.shape = null;
                    }
                }
            }
        });
    }
}