<script>
import { controlEventBus } from '../main';

export default {
    data() {
        return {
            select: true,
            panSelect: false,
            panToggle: false,
            center: false,
            rect: false,
            ellipse: false,
            line: false,
            jump: false,
            top: document.documentElement.clientHeight / 2,
        }
    },
    directives: {
        
    },
    methods: {
        clear: function() {
            this.select = false; this.panSelect = false; this.rect = false; this.ellipse = false; this.line = false; this.jump = false; 
            controlEventBus.imagePanRelease();
        },
        selectClick: function() {
            controlEventBus.drawSelect();
            controlEventBus.imagePanRelease();
            this.clear();
            this.select = true;
        },
        panClick: function() {
            controlEventBus.imagePanPress();
            this.clear();
            this.panSelect = true;
        },
        centerClick: function() {
            controlEventBus.imagePlace();
        },
        rectClick: function() {
            controlEventBus.drawRect();
            controlEventBus.imagePanRelease();
            this.clear();
            this.rect = true;
        },
        ellipseClick: function() {
            controlEventBus.drawEllipse();
            controlEventBus.imagePanRelease();
            this.clear();
            this.ellipse = true;
        },
        polygonClick: function() {
            //TODO: polygon
        },
        lineClick: function() {
            controlEventBus.drawLine();
            controlEventBus.imagePanRelease();
        },
        jumpClick: function() {
            controlEventBus.drawJump();
            controlEventBus.imagePanRelease();
        }
    },
    computed: {
        pan: function() {
            return (this.panSelect || this.panToggle);
        },
    },
    mounted() {
        controlEventBus.$on('drawSelect', () => {
            this.clear();
            this.select = true;
        });
        controlEventBus.$on('imagePanPress', () => {
            this.panToggle = true;
        });
        controlEventBus.$on('imagePanRelease', () => {
            this.panToggle = false;
        })
        controlEventBus.$on('drawRect', () => {
            this.clear();
            this.rect = true;
        });
        controlEventBus.$on('drawEllipse', () => {
            this.clear();
            this.ellipse = true;
        });
        controlEventBus.$on('drawLine', () => {
            this.clear();
            this.line = true;
        });
        controlEventBus.$on('drawJump', () => {
            this.clear();
            this.jump = true;
        })
    },
    created() {
    },
}
</script>

<style scoped>
