//https://developer.mozilla.org/en-US/docs/Web/API/KeyboardEvent
import { controlEventBus } from '../main.js';

export default class Control {
    constructor() {
        document.addEventListener('keydown', (event) => {
            switch(event.target.tagName) {
                case "INPUT": case "SELECT": case "TEXTAREA": return;
            }
            const keyName = event.key;
            switch (keyName) {
                case " ":
                    controlEventBus.imagePanPress();
                    break;
                case "Shift":
                    controlEventBus.shiftPress();
                    break;
                default:
                    return;
            }

        }, false);

        document.addEventListener('keyup', (event) => {
            switch(event.target.tagName) {
                case "INPUT": case "SELECT": case "TEXTAREA": return;
            }
            const keyName = event.key;
            switch (keyName) {
                case "c":
                    controlEventBus.imagePlace();
                    break;
                case "r":
                    controlEventBus.drawRect();
                    break;
                case "e":
                    controlEventBus.drawEllipse();
                    break;
                case "p":
                    controlEventBus.drawPolygon();
                    break;
                case "m":
                    controlEventBus.drawSelect();
                    break;
                case "l":
                    controlEventBus.drawLine();
                    break;
                case " ":
                    controlEventBus.imagePanRelease();
                    break;
                case "Shift":
                    controlEventBus.shiftRelease();
                    break;
                case "d":
                    controlEventBus.backspace();
                    break;
                case "j":
                    controlEventBus.drawJump();
                    break;
                default:
                    return;
            }

        }, false);
    }
}

