<!-- https://www.positronx.io/vue-image-preview-tutorial/ -->
<!-- https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input/file -->

<template>
    <div>
        <div @click="selectImage" v-if="!imgLoaded" id="imageload">
            <input 
                ref="imgLoad" 
                type="file"
                accept=".png"
                @input="storeImage">
        </div>
    </div>
</template>

<script>
import { fileEventBus } from '../main.js';
import { objectEventBus } from '../main.js';

export default {
    data: function () {
        return {
            img: {
                img: new Image(),
                name: '',
                type: null
            },
            imgLoaded: false,
        }
    },
    methods: {
        selectImage: function() {
            this.$refs.imgLoad.click();
        },
        storeImage: function() {
            let input = this.$refs.imgLoad;
            let file = input.files;
            let img = new Image();

            if (file && file[0]) {
                let reader = new FileReader;
                reader.fileName = file[0].name;
                reader.onload = e => {
                    img.src = e.target.result;
                    this.img.name = e.target.fileName;
                }
                reader.onloadend = () => {
                    this.img.img = img;
                    this.imgLoaded = true;
                    this.img.type = file[0].type;
                    this.img.file = file[0]
                    this.submitImage();
                }
                reader.readAsDataURL(file[0]);
            }
        },
        submitImage: function() {
            fileEventBus.imageLoaded(this.img);
            objectEventBus.createBoard(this.img);
        }
    },
}
</script>

<style scoped lang="scss">
  #imageload {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      height: 100%;
      width: 100%;
      z-index: 999;
  }
    input {
        visibility: hidden;
    }
</style>