</template>

<script>
import { objectEventBus } from '../main.js';
import ImageLoad from './ImageLoad.vue';
import ObjectManager from './ObjectManager.vue';
import Analysis from './Analysis.vue';
import Database from './Database.vue';

export default {
    data() {
        return {
            toggled: false,
            selected: new Array, //Array of what is currently selected, passed in from ObjectEventBus
            board: null,
            json: null,
            sidebar: null,
            sidebarButton: null,
            jsonButton: null,
            boardButton: null,
            dbButton: null,
            drag: 0,
        }
    },
    computed: {
        title: function() {
            if (this.selected) {
                /*if(this.selected[1]) {
                    let start = this.selected[0].space.id.toString();
                    let end = this.selected[this.selected.length - 1].space.id.toString();
                    return start + " - " + end;
                }*/
                if (this.selected[0] && this.selected[0].space) {
                    if (!this.selected[0].space.number) return this.selected[0].space.id.toString();
                    return this.selected[0].space.number.toString();
                }
                if (this.selected[0] && this.selected[0].connection) {
                    var conn = this.selected[0].connection;
                    var from = (conn.origin.space.number && conn.origin.space.number != "") ? conn.origin.space.number.toString() : conn.origin.space.id;
                    var to = (conn.origin.space.number && conn.target.space.number != "") ? conn.target.space.number.toString() : conn.target.space.id;
                    return from.toString() + "  " + to;
                }
            }
            if (this.board) {
                return this.board.title;
            }
            return "";
        },
        width: function() {
            if (!this.drag == 0) {
                console.log(this.drag);
            }
            return '320px';
        },
        nothingSelected: function() {
            if (this.selected.length == 0) return true;
            return false;
        }
    },
    components: {
        ObjectManager,
        ImageLoad,
        Analysis,
        Database
    },
    mounted() {
        this.jsonButton = document.getElementById('jsonUpload');
        this.jsonButton.addEventListener('change', this.uploadJson);
        this.boardButton = document.getElementById('boardUpload');
        this.boardButton.addEventListener('change', this.uploadBoard);
        this.sidebarButton = Array.from(document.getElementsByName('sidebarButton'));
    },
    created() {
        objectEventBus.$on('mouseShape', (obj) => {
            this.mouse = obj;
        });
        objectEventBus.$on('selectShape', (shape) => {
            var equals = false;
            if (shape.length == this.selected.length &&
            shape.every((val, index) => val == this.selected[index])) equals = true;
            if (!equals) {
                if (this.toggled && !shape[0]) {
                    document.getElementsByName("sidebarButton")[0].click();
                } 
                if (!this.toggled && shape[0]) {
                    //document.getElementsByName("sidebarButton")[0].click();
                }
            }
            if (equals) {
                if (!this.toggled && shape[0]) {
                    //document.getElementsByName("sidebarButton")[0].click();
                }
            }
            this.selected = shape;
        });
        objectEventBus.$on('selectBoard', (board) => {
            this.board = board;
        });
        objectEventBus.$on('deselect', () => {
            this.selected = new Array;
        });
    },
    methods: {
        toggle() {
        },
        dragged() {
        },
        download() {
            objectEventBus.download();
        },
        buttonBoard() {
            document.getElementById('boardUpload').click();
        },
        buttonJson() {
            document.getElementById('jsonUpload').click(); 
        },
        buttonDb() {

        },
        uploadBoard() {

        },
        uploadJson() {
            let files = this.jsonButton.files;
            if (files.length === 0) return;
            this.json = files[0];
            objectEventBus.upload(this.json);
        },
        analysisToggle() {
            if (this.toggled) this.toggled = false;
        },
        databaseToggle() {
            if (this.toggled) this.toggled = false;
        }
    }
}
</script>

