export default class Path 
{
    spaces = []
    connections = []
    subPaths = []

    constructor(spaces, connections) 
    {
        this.spaces = spaces
        this.connections = connections
    }

    findOptimalPath()
    {
        //todo
    }

    pathString()
    {
        let pathString = ""
        this.spaces.forEach(space => pathString += (space.id + ", "))
        pathString = pathString.slice(0, pathString.length - 2)
        return pathString
    }

    connectionsString()
    {
        let connectionString = ""
        this.connections.forEach(connection => 
            {
                connectionString += connection.origin.id + "  " + connection.target.id + ", "
            })
        connectionString = connectionString.slice(0, connectionString.length - 2)
        return connectionString
    }
}