import Vue from 'vue'
import App from './App.vue'
import router from './router'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.config.productionTip = false;

export const objectEventBus = new Vue({
  methods: {
    createBoard(img) {
      this.$emit('createBoard', img);
    },
    selectBoard(board) {
      this.$emit('selectBoard', board);
    },
    createSpace(space) {
      this.$emit('createSpace', space);
    },
    delete(obj) {
      this.$emit('delete', obj);
      this.deselect();
    },
    clearLines() {
      this.$emit('clearLines');
    },
    createConnection(connection) {
      this.$emit('createConnection', connection);
    },
    connectionsList(connections) {
      this.$emit('connectionsList', connections);
    },
    selectShape(shape) {
      this.$emit('selectShape', shape);
    },
    deselect() {
      this.$emit('deselect');
    },
    download() {
      this.$emit('download');
    },
    upload(file) {
      this.$emit('upload', file);
    },
    submit()
    {
      this.$emit('submit')
    },
    jsonToCanvas(json) {
      this.$emit('jsonToCanvas', json);
    },
    getObjects(obj) {
      this.$emit('getObjects', obj);
    },
    getCanvas(obj)
    {
      this.$emit('getCanvas', obj)
    }
  }
});

export const controlEventBus = new Vue( {
  methods: {
    drawRect() {
      this.$emit('drawRect');
    },
    drawEllipse() {
      this.$emit('drawEllipse');
    },
    drawPolygon() {
      this.$emit('drawPolygon');
    },
    drawSelect() {
      this.$emit('drawSelect');
    },
    drawLine() {
      this.$emit('drawLine');
    },
    drawJump() {
      this.$emit('drawJump');
    },
    imagePlace() {
      this.$emit('imagePlace');
    },
    imagePanPress() {
      this.$emit('imagePanPress');
    },
    imagePanRelease() {
      this.$emit('imagePanRelease');
    },
    shiftPress() {
      this.$emit('shiftPress');
    },
    shiftRelease() {
      this.$emit('shiftRelease');
    },
    backspace() {
      this.$emit('backspace');
    },
  }
});

export const fileEventBus = new Vue( {
  props: {
    img: {}
  },
  methods: {
    imageLoaded(img) {
      this.img = img
      this.$emit('imageLoaded', img);
    },
    getImage()
    {
      return this.img
    }
  }
});

Vue.use(BootstrapVue);
Vue.use(IconsPlugin);

new Vue({
  router,
  el: '#app',
  render: h => h(App)
})