<!-- http://fabricjs.com/fabric-intro-part-5 -->
<!-- https://stackoverflow.com/questions/35218368/how-to-add-image-to-fabric-canvas -->
<!-- https://stackoverflow.com/questions/32207966/how-to-zoom-in-and-center-on-an-object-with-fabricjs -->
<template>
    <div id="container" position="relative">
         <canvas id="c"></canvas>
    </div>
</template>

<script>
import { fabric } from 'fabric';
import { objectEventBus } from '../main.js';
import { controlEventBus } from '../main.js';
import { fileEventBus } from '../main.js';

import DrawShape from '../classes/DrawShape';
import DrawConnection from '../classes/DrawConnection';

import ImageContainer from '../classes/ImageContainer.js';

export default {
    data() {
        return {
            //objects
            img: ImageContainer,
            canvas: fabric.Canvas,
            connections: null,

            //buffers
            deselect: [],

            //info
            width: document.documentElement.clientWidth,
            height: document.documentElement.clientHeight,
            relativeWidth: 1,//5/6, //TODO: make this draggable (in file drawer)

            //controls
            hasMoved: false,
            isPanning: false,
            drawShape: DrawShape,
            drawConnection: DrawConnection,
        }
    },
    computed: {
    },
    components: {
    },
    methods: {
        controlEventBusSetup: function() {
            controlEventBus.$on('imagePlace', () => {
                if (this.img.img == undefined) return;
                this.canvasCenter();
            });
            controlEventBus.$on('imagePanPress', () => {
                //this.cursor = "pointer"; //TODO: the cursor be a hand
                this.isPanning = true;
            });
            controlEventBus.$on('imagePanRelease', () => {
                //this.cursor = "default";
                this.isPanning = false;
            });
            controlEventBus.$on('drawRect', () => {
                this.controlSetActive('drawRect');
            });
            controlEventBus.$on('drawEllipse', () => {
                this.controlSetActive('drawEllipse');
            });
            controlEventBus.$on('drawPolygon', () => {

            });
            controlEventBus.$on('drawLine', () => {
                this.controlSetActive('drawLine');
            });
            controlEventBus.$on('drawJump', () => {
                this.controlSetActive('drawJump');
            });
            controlEventBus.$on('drawSelect', () => {
                this.controlSetActive(null);
            });
            controlEventBus.$on('shiftPress', () => {
                this.canvas.shift = true;
            });
            controlEventBus.$on('backspace', () => {
                if (this.canvas.getActiveObjects()) {
                    var temp = this;
                    this.canvas.getActiveObjects().forEach(function(o) { temp.canvas.remove(o); objectEventBus.delete(o); });
                }
            });
            controlEventBus.$on('shiftRelease', () => {
                this.canvas.shift = false;
            });
        },
        
        fileEventBusSetup: function() {
            fileEventBus.$on('imageLoaded', (img) => {
                this.imageSetup(img)
            });   
        },

        objectEventBusSetup: function() {
            objectEventBus.$on('connectionsList', (connections) => 
            {
                this.connections = connections;
            })
            objectEventBus.$on('jsonToCanvas', (json) => 
            {
                json.CanvasObjects.forEach((shape) => 
                {
                    let add = {};
                    if (shape.type == 'ellSpace') add = new fabric.EllSpace(shape);
                    else if (shape.type == 'rectSpace') add = new fabric.RectSpace(shape);
                    else  { return }
                    let space = json.Spaces.find(space => space.id == add.id);
                    space.shape = add;
                    add.space = space;
                    this.canvas.add(add);
                });
                json.Connections.forEach((connection) => 
                {
                    let origin = json.Spaces.find(space => space.id == connection.origin).shape;
                    connection.origin = origin;
                    let target = json.Spaces.find(space => space.id == connection.target).shape;
                    connection.target = target;
                })
                objectEventBus.connectionsList(json.Connections);
            })
            objectEventBus.$on('getCanvas', (obj) =>
            {
                obj.imageCanvas = this
                obj.canvas = this.canvas
            })
        },
        canvasSetup: function() {
            this.canvas = new fabric.Canvas('c', { 
                selection: false,
                backgroundColor: '#facade',
                shift: false,
                preserveObjectStacking: true,
                width: document.documentElement.clientWidth * this.relativeWidth,
                height: document.documentElement.clientHeight, });
            this.canvasPanSetup();

            this.$nextTick(() => {
                this.canvas.renderAll();
            });
        },

        canvasResize: function() {
            this.width = document.documentElement.clientWidth * this.relativeWidth;
            this.height = document.documentElement.clientHeight;
            this.canvas.setWidth(this.width);
            this.canvas.setHeight(this.height);
            this.canvas.calcOffset();
            this.$nextTick(() => {
                this.canvas.renderAll();
                if (!this.hasMoved && this.img.img != undefined) this.canvasCenter();
            });
        },

        canvasCenter: function() {
            //TODO: revisit
            this.hasMoved = false;
            this.canvas.setZoom(1);
            var img = this.img.img.img; //why did i do this lol
            
            var width = document.documentElement.clientWidth;
            var height = document.documentElement.clientHeight;
            var imgZoom = Math.max(img.width, img.height);
            var canvasZoom = Math.max(width, height);
            var zoomFactor = 0.75;
            var zoom = (canvasZoom / imgZoom) * zoomFactor;

            var vpw = this.width / zoom;
            var vph = this.height / zoom;
            var x = (img.x - vpw / 2) + (img.width / 2);
            var y = (img.y - vph / 2) + (img.height / 2); 
            
            this.canvas.absolutePan({ x: x, y: y });
            this.canvas.setZoom(zoom);
        },

        imageSetup: function(img) {
            this.img = new ImageContainer(this.canvas, img, this.canvasCenter);
        },

        controlSetup: function() {
            this.drawShape = new DrawShape(this.canvas);
            this.drawConnection = new DrawConnection(this);
        },

        canvasSelectSetup: function() {
            this.canvas.on('mouse:up', function() {
                var activeObjects = this.getActiveObjects();
                if (activeObjects[0]) objectEventBus.selectShape(this.getActiveObjects());
                else objectEventBus.deselect();
            });
        },

        canvasZoomSetup: function() {
            var temp = this;
            this.canvas.on('mouse:wheel', function(opt) {
                temp.hasMoved = true;
                var delta = opt.e.deltaY;
                var zoom = this.getZoom();
                zoom *= 0.999 ** (delta * 3);
                if (zoom < 0.1) zoom = 0.1;
                if (zoom > 20) zoom = 20;
                this.zoomToPoint({ x: opt.e.offsetX, y: opt.e.offsetY }, zoom);
                opt.e.preventDefault();
                opt.e.stopPropagation();
            });
        },

        canvasPanSetup: function() {
            var temp = this;
            this.canvas.on('mouse:down', function(opt) {
            var evt = opt.e;
            if (temp.isPanning) {
                this.isDragging = true;
                this.selection = false;
                this.lastPosX = evt.clientX;
                this.lastPosY = evt.clientY;
            }
            });
           this.canvas.on('mouse:move', function(opt) {
            if (this.isDragging) {
                temp.hasMoved = true;
                var evt = opt.e;
                var vpt = this.viewportTransform;
                vpt[4] += evt.clientX - this.lastPosX;
                vpt[5] += evt.clientY - this.lastPosY;
                this.requestRenderAll();
                this.lastPosX = evt.clientX;
                this.lastPosY = evt.clientY;
            }
            });
            this.canvas.on('mouse:up', function() {
                this.setViewportTransform(this.viewportTransform);
                this.isDragging = false;
                this.selection = true;
            });
        },

        controlSetActive: function(control) {
            if (this.canvas.isDown == true) return;
            this.canvas.activeTool = control;
            this.setSelectable();
            console.log('activeTool: ' + this.canvas.activeTool);
        },
        
        //setSelectable(): if: selected line or jump tool,
        //for every selectable object in the canvas,
        //make not selectable, and store the object's index
        //else: make selectable every object whose index is stored
        setSelectable: function() {
            if ((this.canvas.activeTool == 'drawLine' || this.canvas.activeTool == 'drawJump') && this.deselect.length == 0) {
                //populate with indeces of objects where evented = true, set to false
                this.canvas.discardActiveObject();
                this.canvas.getObjects().forEach(object => {
                    if (object.selectable && object.evented && object.space != undefined) {
                        object.selectable = false;
                        object.evented = false;
                        this.deselect.push(object.space.id);
                    }
                })
                this.canvas.selection = false;
            } else if (!(this.canvas.activeTool == 'drawLine' || this.canvas.activeTool == 'drawJump') && this.deselect.length > 0) {
                //set objects at array indeces in deselect to evented = true
                this.canvas.getObjects().forEach(object => {
                    if (object.space && this.deselect.includes(object.space.id)) {
                        object.selectable = true;
                        object.evented = true;
                    }
                })
                this.deselect = [];
                this.canvas.selection = true;
            }
        }
    },
    created() {
        this.controlEventBusSetup();
        this.fileEventBusSetup();
        this.objectEventBusSetup();
    },
    mounted() {
        this.canvasSetup();
        this.canvasSelectSetup();
        this.canvasZoomSetup();
        this.controlSetup();
        window.addEventListener('resize', this.canvasResize);
    },
}
</script>

